import { getOctokit } from "@actions/github";
export class GitHub {
    config;
    octokit;
    constructor(config) {
        this.config = config;
        this.octokit = getOctokit(config.token);
    }
    async listPullRequestFiles(num) {
        const data = await this.octokit.paginate("GET /repos/{owner}/{repo}/pulls/{pull_number}/files", {
            owner: this.config.owner,
            repo: this.config.repo,
            pull_number: num,
        });
        return data;
    }
    async listPullRequestComments(num) {
        const data = await this.octokit.paginate("GET /repos/{owner}/{repo}/pulls/{pull_number}/comments", {
            owner: this.config.owner,
            repo: this.config.repo,
            pull_number: num,
        });
        return data;
    }
    async createPullRequestComment(params) {
        await this.octokit.request("POST /repos/{owner}/{repo}/pulls/{pull_number}/comments", {
            owner: this.config.owner,
            repo: this.config.repo,
            pull_number: params.num,
            body: params.body,
            path: params.filename,
            commit_id: params.sha,
            subject_type: "file",
        });
    }
    async updatePullRequestComment(params) {
        await this.octokit.rest.pulls.updateReviewComment({
            owner: this.config.owner,
            repo: this.config.repo,
            comment_id: params.commentId,
            body: params.body,
        });
    }
}
